* {
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
}

.navbar-bg {
  background-color: #c7f7eb;
}

.navbar .logo {
  margin-left: 4.2rem !important;
  font-size: 20px;
}

ul .comp a {
  margin-right: 50px;
}

ul li a {
  color: black !important;
  font-weight: bold;
}

.btn-dark {
  border-radius: 20px;
  background-color: #17bdc6 !important;
  font-weight: normal;
  color: white !important;
}



.container {
  padding-top: 10;
}
.Home {
  background: linear-gradient(250deg, #86e7cf 0%, #ffffff 60%);
}
.Home-box .Home {
  padding-top: 60px;
}
.Home-flex {
  padding: 2vh;
  flex-wrap: wrap;
}

.Home-flex .container {
  overflow: hidden;
  color: #202124;
  position: relative;
  width: 100vh;
  margin: 0 !important;
}

.Home-flex .container1 {
  margin-left: 2.8rem !important;
}

.Home-flex .container1 h1 {
  font-size: 80px;
}
.Home-flex .container1 p {
  font-size: 20px;
  font-weight: 300;
}
.home-sign-up .btn1 {
  background-color: #17bdc6;
}
.home-sign-up .btn2 {
  color: #8e8e8e;
}

.Home-works h1 {
  font-size: 50px;
  font-weight: 1000;
}

.Home-works {
  padding: 2vh;
  flex-wrap: wrap;
}

.Home-works .container {
  overflow: hidden;
  color: #202124;
  position: relative;
  width: 100vh;
  margin: 0 !important;
}

.Home-works .content {
  height: 50vh;
  border-radius: 3rem;
  background-color: #86e7cf;
}

.Home-works .container2 p {
  font-size: 20px;
  font-weight: 300;
}

.Signup-box {
}

.Signup-flex {
  background: linear-gradient(250deg, #86e7cf 0%, #ffffff 80%);
  flex-wrap: wrap;
  position: absolute;
  width: 100% !important;
}

.Signup-flex .container {
  margin-left: 38rem;
}

.Form-flex {
  position: absolute;
  left: 20rem;
  top: 4rem;
}

.Form-flex h1 {
  font-size: 50px;
}

.Form-flex .container {
  background-color: white;
  margin: 0 !important;
  width: 65vh;
  border-radius: 15px;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
}
form .form-control {
  background-color: white;
  border-radius: 10px;
}

.sign-up .btn {
  background-color: #6a6a6a;
  color: white;
  border-radius: 10px;
}

.sign-up .btn2 {
  background-color: #c4c4c4;
  border-radius: 10px;
  color: black;
}

